.flex{
  height: auto;
  width: auto;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}
.flex2{
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}
.aboutme{
  font-size: medium;
  text-align: center;
  text-decoration: underline;
  margin: 10px;
}
.footer{
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  margin: 10px;
  padding: 5px;
}
.wrap1{
  margin-left: 5px ;
  padding: 5px;
}
.footer{
  background-color: beige;
}
